import './Skills.scss';
import Pages from '../../animations/Pages';

import { Link } from 'react-router-dom';

export default function Skills() {
  return (
    <Pages>
    <section className='Skills'>
      <div className="container">
        <h2 className="text-white">
          My Skills
        </h2>
        <h3>
          Full-Stack Developer
        </h3>
        <i>
          The professional you call when you need a fully-functional Web Application
        </i>
        <div className="row">
          <div className="vic-tech col-xl-3 mt-4">
            <h3 className='vic-title'>Web Technologies</h3>
            <div className="vic-bar">
              <div className="vic-progress" id='html' >
                HTML <span>90%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='css'>
                CSS <span>80%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='js'>
                JS <span>65%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='react'>
                React <span>55%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='angular'>
                Angular <span>45%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='bootstrap'>
                Bootstrap <span>65%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='php'>
                PHP <span>50%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='mysql'>
                MySQL <span>60%</span>
              </div>
            </div>
            <div className="vic-bar">
              <div className="vic-progress" id='cPanel'>
                cPanel <span>75%</span>
              </div>
            </div>
          </div>
          <div className="col-xl-9 mt-4">
            <h3 className='vic-title'>Practical Experience</h3>
            <div className="row">
              <div className="col-xl-4 mb-4">
                <div className="card">
                  <div className="card-header">
                    <h4>Research</h4>
                  </div>
                  <div className="card-body">
                    <h4>Software Development</h4>
                    <ul>
                      <li>Systems Analysis and Design</li>
                      <li>Technical Programming</li>
                      <li>Database Management</li>
                      <li>Project Management</li>
                    </ul>
                  </div>
                  <div className="card-footer">
                    2012 - 2016
                  </div>
                </div>
              </div>
              <div className="col-xl-4 mb-4">
                <div className="card">
                  <div className="card-header">
                    <h4>Design</h4>
                  </div>
                  <div className="card-body">
                    <ul>
                      <li>React Tutorial and Projects Course</li>
                      <li>The Complete React 18 Full Stack Course</li>
                      <li>The Complete Digital Marketing Course</li>
                    </ul>
                  </div>
                  <div className="card-footer">
                    2018 - Presently
                  </div>
                </div>
              </div>
              <div className="col-xl-4 mb-4">
                <div className="card">
                  <div className="card-header">
                    <h4>Building</h4>
                  </div>
                  <div className="card-body">
                    <h4>Certificates</h4>
                    <ul>
                      <li>
                        HTML5 and CSS Fundamentals
                        <ul>
                          <li>W3C FEWD Certificate</li>
                        </ul>
                      </li>
                      <li>
                        CS50x
                        CS50 Web Programming with Python and JavaScript
                        <ul>
                          <li>Harvard CS50 Certificate</li>
                        </ul>
                      </li>
                    </ul>
                  </div>
                  <div className="card-footer">
                    2021 - Presently
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-12 mb-4">
            <div className="card">
              <div className="card-footer">
                <span>
                  <Link to='/portfolio' className='btn btn-xl btn-outline-warning'>
                    My Portfolio
                  </Link>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      
    </section>
    </Pages>
  )
}
