import './Navbar.scss';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { motion } from 'framer-motion';

export default function Navbar() {

    const [toggle, setToggle] = useState('vic-links');
    const [toggleBar, setToggleBar] = useState('fa-bars');

    function toggleNav() {
        if (toggle === 'vic-links') {
            setToggle('vic-links vic-resp');
            setToggleBar('fa-window-close');
        } else {
            setToggle('vic-links');
            setToggleBar('fa-bars');
        }
    }

    return (
        <motion.div 
            initial={{opacity:0, y: -100}} 
            animate={{opacity:1, y: 0}} 
            exit={{opacity:0, y: -100}}
            transition={{duration: 1}} 
            className='Navbar'>
            <div className="vic-brand">
                <Link to='#'>Victor<span>&#9812;</span>M</Link>
            </div>
            <div className="vic-toggle">
                <Link to='#' onClick={toggleNav}><i className={'fa ' + toggleBar}></i></Link>
            </div>
            <div className={toggle}>
                <ul>
                    <li>
                        <Link to='/' onClick={toggleNav}>Home</Link>
                    </li>
                    <li>
                        <Link to='skills' onClick={toggleNav}>Skills</Link>
                    </li>
                    <li>
                        <Link to='portfolio' onClick={toggleNav}>Portfolio</Link>
                    </li>
                    <li>
                        <Link to='offer' onClick={toggleNav}>Offer</Link>
                    </li>
                    <li>
                        <Link to='contact' onClick={toggleNav}>Contact</Link>
                    </li>
                </ul>
            </div>
        </motion.div>
    )
}
