import './Contact.scss';
import Pages from '../../animations/Pages';

import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useRef, useState } from 'react';

const regPhone = /^\+?([0-9]{2})\)?[-. ]?([0-9]{2})[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/;

const schema = yup.object().shape({
  fullName: yup.string().min(3, "too short").max(25, "too long").required(),
  email: yup.string().min(10, "too short").max(100, "too long").email("invalid").required(),
  phone: yup.string().min(2, "too short").max(25, "too long").required().matches(regPhone, {message: "+27 99 999 9999"}),
  contact: yup.string().min(10, "too short").max(200, "too long").required()
})

export default function Contact() {

  const { register, handleSubmit, reset, formState: {errors} } = useForm({
    resolver: yupResolver(schema),
  });

  const [validate, setValidate] = useState('');
  const [effect, setEffect] = useState('vic-msg');
  const [check,  setCheck] = useState('vic-msg');

  const frmData = useRef(null);

  function checkValid() {
    setCheck('vic-msg vic-error-msg');
  }

  const submitForm = (data) => {
    var xhr = new XMLHttpRequest();
    xhr.open("POST", "http://localhost/apis/victorm_mail.php", true);
    xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded; charset=UTF-8");

    var car = "name="+data.fullName+"&email="+data.email+"&phone="+data.phone+"&message="+data.contact;

    xhr.onload = function(){
      if(this.status == 200) {
        var msg = JSON.parse(this.responseText).sms;
        setEffect('d-block text-success');
        setValidate(msg);
        setTimeout(msgTimer, 3000);
        frmData.current.reset();
        reset(data.fullName = '', data.email = '', data.phone = '', data.contact = '');
      } else {
        setEffect('d-block texk-danger');
        setValidate(msg);
      }
      function msgTimer(){
        setValidate('');
      }
    }

    xhr.send(car);
  }

  return (
    <Pages>
    <section className='Contact'>
      <div className="container">
        <h2 className="text-white ">
          Contact Me
        </h2>
        <h3>Call, Email, or Send a Message</h3>
        <i>
          Meetings are based on some terms and conditions.
        </i>
        <div className="row">
          <div className="col-xl-4 mt-4">
            <div className="card">
              <div className="card-body">
                <h3 className='text-center'>
                  I am available for all your querries and or requests.
                </h3>
                <div className="card-footer mt-5">
                  <i className='fa fa-mobile fa-2x'></i>
                  <span>
                    <a href="tel:+27726099284">
                      +27 72 609 92 84
                    </a>
                  </span>
                </div>
                <div className="card-footer mt-4">
                  <i className='fab fa-whatsapp fa-2x'></i>
                  <span>
                    <a href="tel:+27649209733">
                      +27 64 920 9733
                    </a>
                  </span>
                </div>
                <div className="card-footer mt-4">
                  <i className='fa fa-envelope fa-2x'></i>
                  <span>
                    <a href="mailto:contact@victorm.co.za">
                      contact@victorm.co.za
                    </a>
                  </span>
                </div>
                <div className="mt-5 row text-center">
                  <div className="col-4">
                    <a href="http://" target="_blank" rel="noopener noreferrer">
                      <i className='fab fa-facebook fa-2x'></i>
                    </a>
                  </div>
                  <div className="col-4">
                    <a href="http://" target="_blank" rel="noopener noreferrer">
                      <i className='fab fa-instagram fa-2x'></i>
                    </a>
                  </div>
                  <div className="col-4">
                    <a href="http://" target="_blank" rel="noopener noreferrer">
                      <i className='fab fa-twitter fa-2x'></i>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-8 mt-5">
            <form onSubmit={handleSubmit(submitForm)} ref={frmData}>
              <div className="form-floating mb-3">
                <input className="form-control" id="fullName" type="text" {...register("fullName")} />
                <label className='d-block' htmlFor="fullName">
                  Name: 
                  <i className="invalid error"> {errors?.fullName?.message}</i>
                </label>
              </div>
              <div className="form-floating mb-3">
                <input className="form-control" id="email" type="email" {...register("email")} />
                <label htmlFor="email">
                  Email:
                  <i className="invalid error"> {errors?.email?.message}</i>
                </label>
              </div>
              <div className="form-floating mb-3">
                <input className="form-control" id="phone" type="tel" {...register("phone")}/>
                <label htmlFor="phone">
                  Phone:
                  <i className="invalid error"> {errors?.phone?.message}</i>
                </label>
              </div>
              <div className="form-floating mb-3">
                <textarea className="form-control" id="message" type="text" rows="3" {...register("contact")}></textarea>
                <label htmlFor="message">
                  Message:
                  <i className="invalid error"> {errors?.contact?.message}</i>
                </label>
              </div>
              <div className={effect}>
                <div className="text-center mb-3">
                    <div className="fw-bolder">{validate}</div>
                </div>
              </div>
              <button className="btn btn-md btn-outline-warning" id="submitButton" type="submit">Send Message</button>
            </form>
          </div>
        </div>
      </div>
    </section>
    </Pages>
  )
}
