import './Portfolio.scss';
import Pages from '../../animations/Pages';

import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { array } from 'yup';

export default function Portfolio() {

  const [projects, setProjects] = useState([]);
  const [col, setCol] = useState();

  useEffect(() => {
    async function fetchProjects() {
      const response = await fetch('https://sudocyberzeus.github.io/apis/portfolio.json');
      const fetchedProjects = await response.json(response);
      setProjects(fetchedProjects);
    }
    fetchProjects();
  },[]);

  return (
    <Pages>
    <section className='Portfolio'>
      <div className="container">
        <h2 className="text-white">
          My Portfolio
        </h2>
        <div className="row">
          {
            projects.map(({pName, pImage, pDescription, pDate, pLink}, index) => {
              return (
                <div className="col-xl-4 mb-4" key={index}>
                  <div className="card">
                    <div className="card-body d-flex align-items-center justify-content-center">
                      <div className="vic-imgOverText">
                        <h3>{pName}</h3>
                        <p>{pDescription}</p>
                      </div>
                      <img src={pImage} alt="Project" className='img-fluid' />
                    </div>
                    <div className="card-footer">
                      {pDate} 
                      <span className='align-right'>
                        <a href={pLink} target="_blank" rel="noopener noreferrer">
                          <i className='fa fa-search'></i>
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
              )
            })
          }
          <div className="col-lg-12 mb-4">
            <div className="card">
              <div className="card-footer">
                <span>
                  <Link to='/offer' className='btn btn-xl btn-outline-warning'>
                    My Offer
                  </Link>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </Pages>
  )
}
