import './Home.scss';
import Pages from '../../animations/Pages';
import Slide from '../../animations/Slide';

import { Link } from 'react-router-dom';
import { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ref } from 'yup';
import { useReducer } from 'react';

const reducer = (state, action) => {
  switch (action.type) {
    case "SHOW":
      return {count: state.count - 1, effect: state.effect = 'slide'};
    case "HIDE":
      return {count: state.count - 1, effect: state.effect = 'active'};  
    default:
      return state;
  }
}

export default function Home() {
  const [projects, setProjects] = useState([]);
  const [width, setWidth] = useState(0);
  const carousel = useRef();

  useEffect(() => {
    async function fetchProjects() {
      const response = await fetch('https://sudocyberzeus.github.io/apis/portfolio.json');
      const fetchedProjects = await response.json(response);
      setProjects(fetchedProjects);
    }
    setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth);
    fetchProjects();
    
  },[projects]);

  const [state, dispatch] = useReducer(reducer, {count: 0, effect: 'slide'});

  const myProjects = projects.map((myPrjs, index) => (
    <div key={index} className={state.effect}>
      <img className={"img-fluid rounded-3" + state.effect} src={myPrjs.pImage} alt={myPrjs.pImage} />
    </div>
  ));


  localStorage.setItem('prjz', JSON.stringify(projects));

  return (
    <Pages>
    <section className='Home'>
      <div className="container">
      <div className="row align-items-center justify-content-center">
          <div className="col-xl-4 mt-lg-5 mb-4">
            <div className="text-center text-xl-start">
              <div className="vic-avatar">
                <img className="img-fluid  mb-3" src="https://sudocyberzeus.github.io/assets/img/banele-mayisela.jpg" alt="..." />
              </div>
              <h1 className="display-5 fw-bolder text-white mb-2">
                Freelence
              </h1>
              <h2 className="display-5 fw-bolder text-white mb-2">
                Web Developmer
              </h2>
              <h3 className="display-5 fw-bolder text-white mb-2">
                Banele Victor Mayisela
              </h3>
              <p className="lead fw-normal text-white-50 mb-2">
                The Profecional You Deserve
              </p>
              <div className="gap-3 d-sm-flex justify-content-sm-center justify-content-xl-start">
                <Link to='/skills' className="btn btn-outline-warning btn-sm px-4 mt-2">My Skills</Link>
              </div>
            </div>
          </div>
          <div className="col-xl-8 vic-slide mt-3">
            <motion.div 
              ref={carousel} 
              className='carousel'
              whileTap={{cursor: "grabbing"}}
              >
              <h3 className="display-5 fw-bolder text-white ps-4 pt-3">
                My Projects
              </h3>
              <hr />
              <motion.div 
                drag='x' 
                dragConstraints={{ right: 0 , left: -width}} 
                className='inner-carousel'
              >
                {
                  projects.map(({pName, pImage, pDescription, pDate, pLink}, index) => {
                    return (
                      <motion.div className="item d-flex justify-content-center align-items-center" key={index}>
                        <img src={pImage} alt={pName} />
                        <div className="prj-name">
                          {pName}
                        </div>
                      </motion.div>
                    )
                  })
                }
              </motion.div>
            </motion.div>
          </div>
        </div>
      </div>
      
    </section>
    </Pages>
  )
}
